import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Gallery, Div, Button, platform, HeaderButton, IOS, Avatar, itemStyle} from '@vkontakte/vkui';
import connect from '@vkontakte/vkui-connect';

import '@vkontakte/vkui/dist/vkui.css';

import author from '../author/author.json';
import vkuiConnect from '@vkontakte/vkui-connect';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24ReplyOutline from '@vkontakte/icons/dist/24/reply_outline';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';

import './books.css';


class AuthorDetail extends React.Component {
	constructor (props) {
		super(props);

		this.state = {
			bookText: null,
			activeStory: "home",
			check: false,
			activePanel: 'feed',
      history: ['feed'],
			page: this.props.bookStartId
		};
	}
	
	share = () => {
		connect.send("VKWebAppShowWallPostBox", {"message": "üòÑ –Ø —á–∏—Ç–∞—é –∫–∞—Ä—Ç–æ—á–∫—É –∞–≤—Ç–æ—Ä–∞ –Ω–µ –≤—ã—Ö–æ–¥—è –∏–∑ –í–ö–æ–Ω—Ç–∞–∫—Ç–µ.\n\nüòâ –ü–æ–ø—Ä–æ–±—É–π —Å–∞–º - https://vk.com/app7031064", "attachments": "https://vk.com/app7031064"});
	}

	backauthor = (e) => { 
		this.setState({ activeStory: e.currentTarget.dataset.to })
		window.scrollTo(0,0)
	}

	goBack = () => {
		const history = [...this.state.history];
		history.pop();
		const activePanel = history[history.length - 1];
		if (activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppDisableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  goForward = (activePanel) => {
		const history = [...this.state.history];
		history.push(activePanel);
		if (this.state.activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppEnableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	
	render () {
		const osname = platform();
		return (
			<Panel id={this.props.id}>
				<PanelHeader 
			left={<HeaderButton onClick={this.props.backauthor} data-to="feed">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}>
			{author[this.props.bookId].title}
		</PanelHeader>
		<Group className="videl" title={author[this.props.bookId].name}>
		<Gallery
                slideWidth="custom"
                style={{ height: 210 }}
              >
                <img type="image" className="ico-author" src={require("../author"+author[this.props.bookId].path)}/>
                <img type="image" className="ico-author" src={require("../author"+author[this.props.bookId].path1)}/>
								<img type="image" className="ico-author" src={require("../author"+author[this.props.bookId].path2)}/>
								<img type="image" className="ico-author" src={require("../author"+author[this.props.bookId].path3)}/>
								<img type="image" className="ico-author" src={require("../author"+author[this.props.bookId].path4)}/>
              </Gallery>
{/*<div style={{ display: 'flex' }}>
							<img className="ico-card" type="image" src={require("../author"+author[this.props.bookId].path)}/>
              <img className="ico-card" type="image" src={require("../author"+author[this.props.bookId].path1)}/>
		</div> */}
				</Group>
				<Group title="–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ–± –∞–≤—Ç–æ—Ä–µ">
					<Div>
				       <span className="info">–†–æ–¥–∏–ª—Å—è: </span><span className="nackl" className="Header__content" className="noperenos"> {author[this.props.bookId].year1}</span>
						   <br></br>
						   <br></br>
						   <span className="info">–£–º–µ—Ä: </span><span className="Header__content" className="nackl" className="noperenos"> {author[this.props.bookId].year2}</span>
						   <br></br>
						   <br></br>
							 <span className="info">–†–æ–¥ –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏: </span><span className="Header__content" className="nackl" className="noperenos"> {author[this.props.bookId].rod}</span>
						   <br></br>
							 </Div>
				</Group>
				<Group title="–ö—Ä–∞—Ç–∫–∞—è –±–∏–æ–≥—Ä–∞—Ñ–∏—è">
         <Div>
				 {author[this.props.bookId].bio}
				 </Div>
				</Group>
				<Div>
				<Button before={<Icon24Newsfeed/>} onClick={this.share} size="xl" level="secondary" stretched style={{ marginRight: 50 }}>–ü–æ–¥–µ–ª–∏—Ç—å—Å—è –∫–∞—Ä—Ç–æ—á–∫–æ–π</Button>
				</Div>			
			</Panel>
        );
    }
}
AuthorDetail.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	backauthor: PropTypes.func.isRequired,
	share: PropTypes.func,
};


export default (AuthorDetail);
